import React from 'react'
import '../css/work.css'
import '../css/style.css'

function Work() {
    return (
        <div className="work">
            <div className="work__background">
                <div className="work__nav">
                    <div className="work__nav__text">
                        <div className="work__nav__text__position">
                            <h2>my work</h2>
                        </div>
                    </div>
                </div>
                <div className="work__cards">
                    <div className="work__cards__position">
                        <div className="work__cards__flex">
                            <div className="work__cards__flex1">
                                <div className="work__card__text__in__card">
                                    <div className="work__image__1"></div>
                                </div>
                            </div>
                            <div className="work__cards__flex2">
                                <div className="work__card__text__in__card">
                                    <div className="work__image__2"></div>
                                </div>
                            </div>
                            <div className="work__cards__flex3">
                                <div className="work__card__text__in__card">
                                    <div className="work__image__3"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Work
